<template>
  <div class="container">
    <DashboardHeader />
    <DashboardHeroine />
    <DashboardProgress />
    <section class="dashboard-box-bg">
      <section class="dashboard-box">
        <el-row type="flex" justify="center" class="row-bg">
          <el-col :xs="22" :sm="22">
            <DashboardSupply />
          </el-col>
        </el-row>
        <el-row type="flex" justify="center" class="row-bg">
          <el-col :xs="22" :sm="22">
            <DashboardBorrow />
          </el-col>
        </el-row>
      </section>
    </section>
    <!-- <DashboardFooter /> -->
  </div>
</template>

<script>
import DashboardHeader from "@/components/DashboardComponent/DashboardHeader.vue";
import DashboardHeroine from "@/components/DashboardComponent/DashboardHeroine.vue";
import DashboardProgress from "@/components/DashboardComponent/DashboardProgress.vue";
import DashboardSupply from "@/components/DashboardComponent/DashboardSupply.vue";
import DashboardBorrow from "@/components/DashboardComponent/DashboardBorrow.vue";
// import DashboardFooter from "@/components/DashboardComponent/DashboardFooter.vue";
import { connect } from "@/utils/ethereum.js";
// import { getMarkets } from "@/compound/compound.js";
import { mapGetters, mapMutations } from "vuex";
import { USET_DECIMAL_PLACES } from "@/config/constants";

export default {
  name: "Header",
  components: {
    DashboardHeader,
    DashboardHeroine,
    DashboardProgress,
    DashboardSupply,
    DashboardBorrow,
    // DashboardFooter,
  },
  data() {
    return {
      USET_DECIMAL_PLACES: USET_DECIMAL_PLACES,
      timer: null,
    };
  },
  computed: {
    ...mapGetters(["chainID", "address", "markets", "contracts"]),
  },
  mounted() {
    console.log("--- -- apps created -- ---");
    // connect(this);
    // if (this.chainID !== "0x539") {
    //   const h = this.$createElement;
    //   this.$notify.error({
    //     title: "error",
    //     message: h(
    //       "i",
    //       { style: "color: teal" },
    //       "AoLend 目前只能运行在 0x539 测试网络中"
    //     ),
    //     duration: 0,
    //   });
    //   return false;
    // }
    setTimeout(() => {
      connect(this).then(() => {
        this.init(this.address);
        this.timer = setInterval(() => {
          this.init(this.address);
        }, 6000);
      });
    }, 0);
  },
  destroyed() {
    console.log("--- destroyed ---");
    clearInterval(this.timer);
  },
  methods: {
    ...mapMutations([
      "set_chainID",
      "set_address",
      "set_balance",
      "set_supplyBalance",
      "set_netAPY",
      "set_borrowBalance",
      "set_globalInfo",
      "set_borrowed",
      "set_totalLimit",
      "set_borrowedLimitPercent",
      "set_markets",
      "set_contracts",
    ]),
    async init(address) {
      console.log("-- dashboard init --");
      let info = await window.clens.marketInfo(address);
      this.set_globalInfo(info);
      let supplyBalance = info.supplyBalance.toString();
      this.set_supplyBalance(supplyBalance);
      let netAPY = info.netAPY.toString();
      this.set_netAPY(netAPY);
      let borrowBalance = info.borrowBalance.toString();
      this.set_borrowBalance(borrowBalance);
      this.getBorrowLimit(info);
      let markets = info.markets;
      this.set_markets(markets);
      let contracts = window.contracts;
      this.set_contracts(contracts);
    },
    async getBorrowLimit(info) {
      let borrowLimit = await info.borrowLimit();
      let borrowed = borrowLimit[0];
      this.set_borrowed(borrowed);
      let totalLimit = borrowLimit[1];
      this.set_totalLimit(totalLimit);
      let borrowedLimitPercent = borrowLimit[2];
      this.set_borrowedLimitPercent(borrowedLimitPercent);
    },
  },
};
</script>

